.m-home-wrapper{
	.m-home-banner{
		width: 100%;
		img {
			width: 100%
		}
		position: relative;
		a{
			position: absolute;
			width: 10vw;
			height: 10vw;
			right: 20px;
			bottom: 20px
		}
	}
	.m-home-nav{
		padding: 10px;
		background: #1a1c1d;
		a{
			
		}
		.m-nav-main{
			display: flex;
			width: 100%;
			max-width: 1000px;
			margin: 0 auto;
			li {
				color: #fff;
				font-size: 16px;
				text-align: center;
				flex: 1;
				cursor: pointer;
				&.active{
					color: #e6b429;
				}
			}
		}
	}
	.m-home-main{
		padding: 0 40px 40px;
		background: url('./images/bg.jpeg');
		background-size: 100%;
		.m-home-wrapper{
			background: #fff;
			width: 98%;
			max-width: 1000px;
			margin: 0 auto;
			padding: 20px 25px;
		}
		.logo{
			max-width: 180px;
			margin: 20px auto 40px;
			display: block;
		}
		.m-home-section{
			margin-bottom: 20px;
		}
		h3{
			text-align: center;
			color: #00376d;
			font-size: 26px;
		}
		.m-home-tag{
			font-size:14px;
			color: #606060;
			text-align: center;
			font-weight: bold;
			margin-bottom: 10px;
		}
		p {
			color: #333;
			font-family: 'Microsoft YaHei';	
			font-size: 15px;
		}
		.m-home-tips{
			color:#00376d;
			font-size: 18px;
			font-weight: 600;
		}
		.m-home-w{
			font-size: 12px;
			color: #666;
			padding: 0 10px;
		}
		.m-work-list{
			max-width: 266px;
			width: 100%;
			margin: 0 auto;
			h4{ 
				background: #00376d;
				font-size: 20px;
				color: #fff;
				font-weight: 500;
				text-align: center;
				padding: 5px 0;
			}
			div{
				position: relative;
				font-size: 13px;
				color: #333;
				padding: 10px;
				background: #eeedee;
				height: 280px;
				text-align: justify;
				&::after{
					position: absolute;
					height: 5px;
					background: #80d2ff;
					left: 20px;
					right: 20px;
					bottom: 0px;
					content: '';
				}
			}
		}
		.m-home-dl {
			dt{
				font-size: 15px;
				margin-bottom: 10px;
				font-weight: bold;
			}
			dd {
				margin-bottom: 15px;
			}
			span{
				color: #00a4ff;
				margin-right: 5px;
			}
		}
	}
	.m-home-footer{
		background: url('./images/foot.jpg') no-repeat ;
		background-size: cover;
		height: 180px;
		color: #fff;
		padding-top: 60px;
		div{
		  margin-bottom: 10px;
		}
	}
}
@root-entry-name: default;@primary-color: #00376d;